import React from 'react';
import { Menu, Icon, Input, Typography, Row, Col, Avatar } from 'antd';
import styled from 'styled-components';

const { Title, Paragraph, Text } = Typography;

const {ItemGroup} = Menu;
const { Search } = Input;



const MainTitle = styled(Typography)`
    color: #ffffff;
    font-size:30px;
    top:0;
    left:0;
    right:0;
    bottom:0;
`;

const HomeIcon = styled(Icon)`
    color: #ffffff;
    font-size:25px;
    text-align: center;
    top:0;
    left:0;
    right:0;
    bottom:0;
`;

const BellIcon = styled(Icon)`
    color: #ffffff;
    font-size:25px;
    text-align: center;
    top:0;
    left:0;
    right:0;
    bottom:0;
`;

const SettingIcon = styled(Icon)`
    color: #ffffff;
    font-size:25px;
    text-align: center;
    top:0;
    left:0;
    right:0;
    bottom:0;
`;

const UserIcon = styled(Avatar)`
    text-align: center;
    top:0;
    left:0;
    right:0;
    bottom:0;
`;

const MenuItem = {
    textAlign:'center'
}


const AppTopbar = () => {

    return(
        <div>
          <Menu mode="horizontal" selectedKeys='home' style={{backgroundColor:'#0B4E92'}} >
            <Row>
            <Col span={2}>
                <ItemGroup key="home" >
                    <HomeIcon type="home" />
                </ItemGroup>
            </Col>
            <Col span={4}>
                <ItemGroup key="search" >
                    <Search
                        placeholder="input search text"
                        style={{ width: 200 }}
                    /> 
                </ItemGroup>
            </Col>
            <Col span={12}>
                <ItemGroup key="subject" >
                    <Typography>
                        <MainTitle>RemindFeedBack</MainTitle>
                    </Typography>
                </ItemGroup>
            </Col>
            <Col span={3}></Col>
            <Col span={1}>
                <ItemGroup key="bell" >
                    <BellIcon type="bell" />
                </ItemGroup>
            </Col>
            <Col span={1}>
                <ItemGroup key="user" >
                    <UserIcon icon="user" />
                </ItemGroup>
            </Col>
            <Col span={1}>
                <ItemGroup key="setting" >
                    <SettingIcon type="setting" />
                </ItemGroup>
            </Col>
            </Row>
          </Menu>  
        </div>
    );
};

export default AppTopbar;